body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 18px;
  
  
}

button {
  padding: 16px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 16px;
  background-color: black;
  color: white;
  border-radius: 8px;
  cursor: pointer;
}

h3, h4 {
  margin-bottom: 0;
}

nav {
  border-bottom: 4px solid black;
  text-align: center;
  position: fixed;
  top: 0;
  width: 100%;
  background: white;
}

nav ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

nav li {
  display: inline-block;
  padding-top: 16px;
  padding-bottom: 16px;
}

nav a {
  text-decoration: none;
  color: black;
  font-weight: bold;
  padding: 16px 32px;
}

nav a:hover {
  background: black;
  color: white;
}

#page-body {
  max-width: 1200px;
  margin: auto;
  padding-top: 24px;
}

.article-list-item {
  text-decoration: none;
  color: black;
  padding: 16px 0 0 0;
}

.article-list-item p {
  padding-bottom: 32px;
  border-bottom: 2px solid black;
}

#add-comment-form {
  border-bottom: 2px solid black;
  padding: 16px;
}

#add-comment-form h3 {
  margin-bottom: 16px;
}

#add-comment-form input, textarea {
  display: block;
  width: 300px;
  padding: 8px;
  border-radius: 8px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 18px;
  border: 2px solid black;
  margin-bottom: 16px;
}

#add-comment-form button {
  width: 320px;
}

#upvotes-section button, p {
  display: inline-block;
  margin-right: 16px;
}

.comment {
  border-bottom: 2px solid black;
  padding: 16px;
}

.font-link {
  font-family: 'Open Sans', sans-serif;
  font-size: 30px;
  margin-top: -30px;
}

.font-title {
  font-family: 'Open Sans', sans-serif;
  font-size: 60px;
  margin-top: 130px;
}

#parent {
  display: flex;
}
#narrow {
  width: 150px;
  display: inline-block;
  margin-top: 17px;

}
#narrowtextleft {
  width: 500px;
  display: inline-block;
  margin-top: 17px;
  font-family: 'Open Sans', sans-serif;

}
#narrowtextright {
  width: 250px;
  display: inline-block;
  margin-top: 17px;
  font-family: 'Open Sans', sans-serif;
}
#wide {
  flex: 5;
  display: inline-block;
}
.thick {
  font-weight: bold;
}

.about-list-item {
  padding-bottom: 32px;
  border-bottom: 2px solid black;
}
.outline {
  outline: 1px solid black;
  border-radius: 22%;
}
.outline2 {
  outline: 1px solid black;
  border-radius: 50%;
}
.contactcontainer {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
}
.contactcontainer2 {
  margin-bottom: 30px;
}